<template>
	<tabbar
		v-if="$route.meta.tabbar"
		:active="active"
		@change="onChange"
		:inactive-color='inactiveColor'
		router
	>
		<tabbar-item name="index" to="/index">
			<img src="../assets/index.png" slot='icon' />
			<span v-if="active == 'index'" class="activeBg activeBg_leftRadius"></span>
		</tabbar-item>
		<tabbar-item name="income" to="/income">
			<img src="../assets/income.png" slot='icon' />
			<span v-if="active == 'income'" class="activeBg activeBg_centerRadius"></span>
		</tabbar-item>
		<tabbar-item name="exchange" to="/exchange">
			<img src="../assets/exchange.png" slot='icon' />
			<span v-if="active == 'exchange'" class="activeBg activeBg_centerRadius"></span>
		</tabbar-item>
		<tabbar-item name="setting" to="/setting">
			<img src="../assets/setting.png" slot='icon' />
			<span v-if="active == 'setting'" class="activeBg activeBg_rightRadius"></span>
		</tabbar-item>
	</tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant'

export default {
  name: 'self-tabbar',
  components: { Tabbar, TabbarItem },
  data() {
    return {
      active: 'index',
	  theme: localStorage.getItem('theme') == null ? 'moon' : localStorage.getItem('theme'),
	  // inactiveColor: localStorage.getItem('theme') == 'moon' ? '#A1BDE1' : '#333333',
	  inactiveColor: localStorage.getItem('theme') == null ? '#A1BDE1' : localStorage.getItem('theme') == 'sun' ? '#333' : '#A1BDE1',
    }
  },
  mounted() {
    this.active = this.$route.name
  },
  methods:{
	onChange(event) {
	    this.active = event
	}
  },
  watch:{
	  $route(){
		  this.active = this.$route.name
	  }
  }
}
</script>

<style lang="scss" scoped>
.van-tabbar{
	background-color: #18A664;
	border: none;
	border-radius: 20px 20px 0 0;
}
.van-tabbar:after{
	border: none;
}
.van-tabbar-item{
	position: relative;
}
.van-iconfont{
  font-size: 22px !important;
}
.van-tabbar-item__icon img{
	height: 22px;
}
.activeBg{
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: rgba(#000000, 0.13);
	left: 0;
	top: 0;
}
.activeBg_leftRadius{
	border-radius: 40px 0 40px 40px;
}
.activeBg_centerRadius{
	border-radius: 0 0 40px 40px;
}
.activeBg_rightRadius{
	border-radius: 0 40px 40px 40px;
}
// .iconHome{
// 	font-size: 48px !important;
// }

</style>